<template>
  <div id="my-souce-1">
    <div class="source-row" v-for="(item, i) in data" :key="i">
      <div class="icons">
        <i class="iconfont icon-Word"></i>
      </div>
      <div>
        <p class="s-title">{{ item.name }}</p>
        <p class="s-title">
          <span>贡献者: {{ item.contributors }}</span>
          <span>上传时间: {{ item.time }}</span>
        </p>
      </div>
      <div class="score">
        <p>{{ item.score }}</p>
        <p>资源评分</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["data"]
};
</script>
<style lang="less">
#my-souce-1 {
  .source-row {
    display: flex;
    margin-bottom: 16px;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .icons {
    width: 60px;
    height: 60px;
    background-color: #2c97ff;
  }
  .icons .iconfont {
    font-size: 30px;
    color: #fff;
    position: relative;
    top: 15px;
  }
  div p:last-child {
    font-size: 12px;
    color: #999999;
  }
  div p:first-child {
    font-size: 15px;
    margin-bottom: 12px;
  }
  .s-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 200px;
    text-align: left;
  }
  .source-row .score p:first-child {
    font-size: 18px;
    color: #07ccad;
    margin-bottom: 4px;
  }
}
</style>



